<script>
  import { forEach, isEmpty, keyBy, map, orderBy } from 'lodash-es';

  import Sankey from '../components/vis/ClusteredSankey.svelte';
  import { regions, regionRegionScopes } from '../data/dataAPI';
  import { tooltipContent } from '../state/uiState';

  let width;
  let height;
  let nodes = [];
  let links = [];

  function makeId(d) {
    return(d.replace(/ /g, '_').toLowerCase());
  }

  $: regionsByName = keyBy($regions, 'name');
  $: isReady = $regionRegionScopes.length !== 0 && $regions.length !== 0  && !isEmpty(regionsByName);
  
  $: {
    nodes = [];
    links = [];

    if(isReady) {

      // add regions
      forEach(orderBy($regions, 'value','desc'), d => {
        const nodeId = makeId(`region-${d.name}`);

        nodes.push({
          id: nodeId, 
          depth: 0,
          name: d.name, 
          value: d.value, 
          type: 'group',
          align: 'left',
          items: orderBy(map(d.categories, e => {
            const itemId = makeId(`${nodeId}-${e.name}`);
            return ({
              id: itemId, 
              name: e.name, 
              color: e.color, 
              value: e.value, 
              align: 'right',
              tooltip: {
                header: `${d.name}: ${e.name}`,
                text: `${e.value} ${e.value > 1 ? 'activities' : 'activity'}`
              }
            })
          }),'value','desc')});
      })


      // add scope regions

      forEach($regionRegionScopes, d => {
        const nodeId = makeId(`regionscope-${d.name}`);
        forEach(d.categories, category => {
          forEach(category.regions, region => {
            const linkId = makeId(`region-${region.name}`);
            const subItemId = makeId(`link-${linkId}-${category.name}-${nodeId}`);
            const sourceCategoryId = makeId(`${linkId}-${category.name}`);
            const targetCategoryId = makeId(`${nodeId}-${category.name}`);
            links.push({
              id: subItemId, 
              source: linkId,   
              target: nodeId, 
              sourceItem: sourceCategoryId, 
              targetItem: targetCategoryId, 
              value: region.value, 
              color: category.color,
              tooltip: {
                header: category.name,
                text: `${region.name} → ${d.name} (${region.value} ${region.value > 1 ? 'activities' : 'activity'})`
              }
            });
          });
        }); 
        nodes.push({
            id: nodeId, 
            name: d.name,
            depth: 1, 
            value: d.value, 
            type: 'group',
            align: 'right',
            items: orderBy(map(d.categories, e => {
              const itemId = makeId(`${nodeId}-${e.name}`);
              return ({
                id: itemId, 
                name: e.name, 
                color: e.color, 
                value: e.value, 
                align: 'left',
                tooltip:  {
                  header: `${d.name}: ${e.name}`,
                  text: `${e.value} ${e.value > 1 ? 'activities' : 'activity'}`
                }
              })
            }),'value','desc')
          });
      })
    }
  }


  $: isTablet = width < 1000;
  $: isMobile = width < 550;
</script>

<style lang="scss">/* breaks */
.chart-container {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box; }
/*# sourceMappingURL=src/views/Sankey.svelte.map */</style>

<div
  class="chart-container"
  bind:offsetWidth="{width}"
  bind:offsetHeight="{height}">
  {#if nodes.length !== 0 && links.length !== 0 }
    <Sankey
      {nodes}
      {links}
      {width}
      {height} 
      {isMobile}
      {isTablet}
      header={['Regions','Supported Regions']}
      margin={isMobile ? { top: 40, right: 70, bottom: 20, left: 40 } : { top: 50, right: 250, bottom: 25, left: 250 }}
      on:hover={(d) => { tooltipContent.set(d.detail)}} 
      on:unhover={(d) => { tooltipContent.set(null)}} 
    />
  {/if}
</div>
