<script context="module">
  import { groupBy, toPairs, forEach,  } from 'lodash-es';

	export function generateSankey(_graph, _config) {
    
    const config = { 
      'nodePadding': 10, 
      'nodeWidth': 15,
      'width': 100,
      'height': 100,
      ..._config
    }
    const height = config.height;
    const width = config.width;

    const graph = {nodes: [], links: [], ..._graph};
    const groupedNodes = toPairs(groupBy(graph.nodes, 'depth'));
    const countGroups = groupedNodes.length;

    // set scale
    const heightScale = getHeightScale(groupedNodes,height,config.nodePadding);
    const widthStep = width / (countGroups - 1);

    // compute node dimension and position

    const nodes = [];
    const nodeLinks = {};

    forEach(groupedNodes, ([index,items]) => {
      let y = 0;
      let x = +index == (countGroups - 1) ? (widthStep * +index) - config.nodeWidth: widthStep * +index;
      forEach(items, (d,i) => {
        const height = heightScale(d.value);
        const subNodes = [];
        let subY = y;
        // sub items
        forEach(d.items, e => {
          const subHeight = heightScale(e.value);
          const node = {
            ...e,
            x0: x,
            x1: x + config.nodeWidth,  
            y0: subY, 
            y1: subY + subHeight, 
            width: config.nodeWidth,
            height: subHeight
          };
          subNodes.push(node);
          nodeLinks[node.id] = {
            x0: x,
            x1: x + config.nodeWidth,  
            y: subY, 
          };

          subY += subHeight;
        })
        nodes.push({
          ...d,
          items: subNodes,
          x0: x,
          x1: x + config.nodeWidth,  
          y0: y, 
          y1: y + height, 
          width: config.nodeWidth,
          height: height
        });
        y += height + config.nodePadding;
      })
    })

    const links = [];
    forEach(graph.links, d => {
      const source = nodeLinks[d.sourceItem];
      const target = nodeLinks[d.targetItem];
      const width = heightScale(d.value);
      links.push({
        ...d, 
        x0: source.x1, 
        x1: target.x0, 
        y0: source.y + width / 2,  
        y1: target.y + width / 2,  
        width: heightScale(d.value)
      });
      source.y += width;
      target.y += width;
    })

    return {nodes, links}
  }

  function getHeightScale(groups,height,padding) {
    let minRatio = 999999;
    forEach(groups, ([groupIndex,group]) => {
      let value = 0;
      forEach(group, (node) => {
        value += node.value;
      });
      const availabelSpace = height - (group.length - 1) * padding;
      minRatio = Math.min(availabelSpace/value,minRatio);
    });
    
    return function(d) {
      return d * minRatio;
    }
  }


</script>

<script>
  import { createEventDispatcher } from 'svelte';
  // import { sankey, sankeyJustify,sankeyLeft, sankeyLinkHorizontal } from 'd3-sankey';
  import { map, uniqBy } from 'lodash-es';
  import {linkHorizontal} from "d3-shape";

  export let width = 0;
  export let height = 0;
  export let nodes = [];
  export let links = [];
  export let header = [];
  export let margin = { top: 0, right: 20, bottom: 20, left: 20 };
  export let isTablet = false;
  export let isMobile = false;

  const dispatch = createEventDispatcher();

  let hoveredNode = null;
  let hoveredNodeItem = null;
  let hoveredLink = null;

  let hoveredNodeIds = {};
  let hoveredLinkIs = {};
  
  $: stageWidth = width - margin.left - margin.right;
  $: stageHeight = height - margin.top - margin.bottom;

  let labelDisplayItems = [];

  $: d3Link = linkHorizontal()
      .source((d) => [d.x0, d.y0])
      .target((d) => [d.x1, d.y1]);

  $: sankey = generateSankey({
      nodes: nodes.map(d => Object.assign({}, d)),
      links: links.map(d => Object.assign({}, d)),
    }, {width: stageWidth, height: stageHeight, nodePadding: 8});
  
  $: {
    labelDisplayItems = [];
    const subLabelPos = {};

    forEach(sankey.nodes, d => {
      if(isMobile) {
        if(d.name !== '' && (d.height / d.name.length) > 4.5) {
          let x, align;
          if(d.align === 'left') {
            x = d.x0 - 15;
            align = 'middle';
          } else {
            x = d.x1 + 15;
            align = 'middle';
          }
          labelDisplayItems.push({id: d.id, x: x, y: d.y0 + d.height/2, text: d.name, align: align, type: 'group'});
        }
      } else {
        if(d.name !== '') {
          let x, align;
          if(d.align === 'left') {
            x = d.x0 - 10;
            align = 'end';
          } else {
            x = d.x1 + 10;
            align = 'start';
          }
          labelDisplayItems.push({id: d.id, x: x, y: d.y0 + d.height/2, text: d.name, align: align, type: 'group'});
        }
      }
      if(!isTablet) {
        forEach(d.items, e => {
          if(e.name !== '' && e.height > 10 && (subLabelPos[e.name] === undefined || subLabelPos[e.name][e.x0] === undefined)) {
            
            // show labels only ones
            if(subLabelPos[e.name] === undefined) {
              subLabelPos[e.name] = {};
            }
            subLabelPos[e.name][e.x0] = 1;

            let x, align;
            if(e.align === 'left') {
              x = e.x0 - 10;
              align = 'end';
            } else {
              x = e.x1 + 10;
              align = 'start';
            }
            labelDisplayItems.push({
              id: e.id, x: x, y: e.y0 + e.height/2, 
              text: e.name, align: align, type: 'item', tooltip: e.tooltip,
            });
          }
        })
      }
    });
    
  }

  // set hovered ids

  $: {
    hoveredNodeIds = {};
    hoveredLinkIs = {};

    if (hoveredNode !== null) {
      // hovered nodes
      forEach(sankey.links, d => {
        if(d.source === hoveredNode.id || d.target === hoveredNode.id) {
          hoveredLinkIs[d.id] = 1;  
          hoveredNodeIds[d.source] = 1;  
          hoveredNodeIds[d.sourceItem] = 1;  
          hoveredNodeIds[d.target] = 1;  
          hoveredNodeIds[d.targetItem] = 1;  
        }
      })
    } else if (hoveredNodeItem !== null) {
      // node item hovered
      forEach(sankey.links, d => {
        if(d.sourceItem === hoveredNodeItem.id || d.targetItem === hoveredNodeItem.id) {
          hoveredLinkIs[d.id] = 1;  
          hoveredNodeIds[d.source] = 1;  
          hoveredNodeIds[d.sourceItem] = 1;  
          hoveredNodeIds[d.target] = 1;  
          hoveredNodeIds[d.targetItem] = 1;  
        }
      })
    } else if (hoveredLink !== null) {
      // single link hovered
      const d = hoveredLink;
      hoveredLinkIs[d.id] = 1;
      hoveredNodeIds[d.source] = 1;  
      hoveredNodeIds[d.sourceItem] = 1;  
      hoveredNodeIds[d.target] = 1;  
      hoveredNodeIds[d.targetItem] = 1;  
    }
  }

  $: isHovered =  hoveredNode !== null || hoveredNodeItem !== null || hoveredLink !== null;

  $: {
    if(hoveredNodeItem !== null) {
      dispatch('hover', {
        header: hoveredNodeItem.tooltip.header,
        items: [
          [
            {type: 'text', value: hoveredNodeItem.tooltip.text},
          ]
        ]
      });
    }

    if(hoveredLink !== null) {
      dispatch('hover', {
        header: hoveredLink.tooltip.header,
        items: [
          [
            {type: 'text', value: hoveredLink.tooltip.text},
          ]
        ]
      });
    }
  }


  $: activeNodes = map(sankey.nodes, d => {

    return {
      ...d, 
      items: map(d.items, e => {        
        return ({...e, isInactive: isHovered && hoveredNodeIds[e.id] === undefined})
      }),
      isInactive: isHovered && hoveredNodeIds[d.id] === undefined
    };
  })

  // $: console.log(activeNodes);

  $: activeLinks = map(sankey.links, d => {
    return {
      ...d, 
      isActive: isHovered && hoveredLinkIs[d.id] !== undefined,
    };
  })

  $: activeLabels = map(labelDisplayItems, d => {
    return {
      ...d, 
      isInactive: isHovered && hoveredNodeIds[d.id] === undefined
    };
  })

  $: headerDisplayItems = map(uniqBy(sankey.nodes, d => d.x0), (d,i) => {
    return {id: i, text: header[i], x: d.x0 + (d.x1 - d.x0) / 2, y: -20}
  });

  $: isReady = activeNodes.length > 0 && activeLinks.length > 0;

</script>

