.sankey {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: visible; }

.sankey__node {
  cursor: pointer;
  transition: opacity 400ms ease-in-out; }
  .sankey__node.is-inactive {
    fill-opacity: 0.6; }

.sankey__link {
  cursor: pointer;
  fill: none;
  stroke-opacity: 0.4;
  transition: opacity 400ms ease-in-out; }
