    opacity: 0.5; }

.sankey__link-fg {
  mix-blend-mode: multiply;
  stroke-opacity: 0.6;
  pointer-events: none;
  fill: none; }

.sankey__labels .is-inactive {
  opacity: 0.3; }

.sankey__label, .sankey__label-bg {
  font-size: 11.86732px; }
  @media (min-width: 320px) {
    .sankey__label, .sankey__label-bg {
      font-size: calc(11.86732px + 1.19662  * (100vw - 320px) / 880); } }
  @media (min-width: 1200px) {
    .sankey__label, .sankey__label-bg {
      font-size: 13.06395px; } }

.sankey__label-bg {
  color: #fff;
  stroke-width: 3;
  stroke: #fff; }

.sankey__label {
  cursor: pointer; }

.sankey__header {
  font-family: "Zilla Slab", serif;
  font-size: 13px;
  text-anchor: middle; }
  @media (min-width: 320px) {
    .sankey__header {
      font-size: calc(13px + 3  * (100vw - 320px) / 880); } }
  @media (min-width: 1200px) {
    .sankey__header {
      font-size: 16px; } }
/*# sourceMappingURL=src/components/vis/ClusteredSankey.svelte.map */</style>


  <svg class="sankey" {width} {height}>
    <g transform="translate({margin.left},{margin.top})">
      {#if isReady}
        <g class="sankey__nodes">
          {#each activeNodes as d (d.id)}
            {#if d.type === 'group'}
              {#each d.items as e (e.id)}
                <rect 
                  class="sankey__node"
                  class:is-inactive={e.isInactive}
                  x={e.x0 + 1} 
                  y={e.y0} 
                  width={e.x1 - e.x0 - 2} 
                  height={e.y1 - e.y0} 
                  fill={e.color} 
                  on:mouseover="{() => {
                    hoveredNodeItem = e;
                  }}"
                  on:mouseout="{() => {
                    hoveredNodeItem = null;
                    dispatch('unhover'); 
                  }}"
                />
              {/each}
            {:else}
              <rect 
                class="sankey__node"
                class:is-inactive={d.isInactive}
                x={d.x0 + 1} 
                y={d.y0} 
                width={d.x1 - d.x0 - 2} 
                height={d.y1 - d.y0} 
                fill={d.color} 
                on:mouseover="{() => {
                  hoveredNodeItem = d;
                }}"
                on:mouseout="{() => {
                  hoveredNodeItem = null;
                }}"
              />
            {/if}    
          {/each}
        </g>
        <g class="sankey__links">
          {#each activeLinks as d (d.id)}
            <path 
              class="sankey__link"
              class:is-inactive={isHovered}
              d={d3Link(d)} 
              stroke={d.color} 
              stroke-width={ Math.max(1, d.width)}
              on:mouseover="{() => {
                hoveredLink = d;
              }}"
              on:mouseout="{() => {
                hoveredLink = null;
                dispatch('unhover'); 
              }}"
            />
          {/each}
        </g>
        {#if isHovered}
        <g class="sankey__active-links">
          {#each activeLinks as d (d.id)}
            {#if d.isActive}
            <path 
              class="sankey__link-fg"
              d={d3Link(d)} 
              stroke={d.color} 
              stroke-width={ Math.max(1, d.width)}
            />
            {/if}
          {/each}
        </g>  
        {/if}      

        <g class="sankey__labels">
          {#each activeLabels as d}
            <g class:is-inactive={d.isInactive}>
              <text 
                class="sankey__label-bg" 
                dy="0.3em" 
                style="transform: translate({d.x}px,{d.y}px)  rotate({isMobile ? '-90deg':'0'})"
                text-anchor={d.align}>
                {d.text}
              </text>
              <text 
                class="sankey__label" 
                dy="0.3em" 
                style="transform: translate({d.x}px,{d.y}px)  rotate({isMobile ? '-90deg':'0'})"
                text-anchor={d.align}
                on:mouseover="{() => {
                  if(d.type === 'group') {
                    hoveredNode = d;
                  } else if (d.type === 'item') {
                    hoveredNodeItem = d;
                  }
                }}"
                on:mouseout="{() => {
                  if(d.type === 'group') {
                    hoveredNode = null;
                  } else if (d.type === 'item') {
                    hoveredNodeItem = null;
                  }
                }}"
              >{d.text}</text>
            </g>
          {/each}
        </g>
   
        <g class="sankey__headers">
          {#each headerDisplayItems as d}
            <text class="sankey__header" x={d.x} y={d.y} dy="0.3em">{d.text}</text>
          {/each}
        </g>
      {/if}
    </g>
  </svg>

